Meteor.publish("discuz", function(id, userid) {
    if (userid)
        return [Discuzs.find({
            _id: id
        }), DiscuzMessages.find({
            discuz: id
        }), DiscuzFovar.find({
            discuz: id,
            "userid": userid,
        })];
    else
        return [Discuzs.find({
            _id: id
        }), DiscuzMessages.find({
            discuz: id
        })];

});

Meteor.publish("discuzUsers", function(id) {

    var ds = Discuzs.findOne({
        _id: id
    });
    return Meteor.users.find({
        _id: {
            $in: ds.users
        }
    });
});
Meteor.publish("discuzList", function(uid) {
    return Discuzs.find({ status: 1 })
});
Meteor.publish("discuzPublic", function(uid) {
    return Discuzs.find({ status: 1, type: "PUBLIC" })
});

Meteor.publish("discuzMyList", function() {
    return Discuzs.find({
        send: 1,
        status: 1
    }, { sort: { createAt: -1 }, limit: 20 });
});
Meteor.publish("discuzMyBox", function(uid) {
    return Discuzs.find({
        send: 0,
        status: 1,
        userid:uid,
    });
});
Meteor.publish("discuzDetail", function(uid) {

    return [
        Meteor.users.find({ _id: uid }),
        Discuzs.find({ userid: uid })
    ]
});
Meteor.publish("recommands", function(uid) {

    return [
        Recommands.find()
    ]
});
