Meteor.publish("myVisits", function(uid) {

    return Visits.find({
        $or: [
            { "executer.userid": uid },
            { "creater.userid": uid }
        ],
        status: 1
    });
});
Meteor.publish("visitDetail", function(id) {
    return Visits.find({
        _id: id
    });
});
