Meteor.publish("visitDetails", function(id) {
    var r = Visits.findOne({
        _id: id,
        status: 1
    });
    if (r) {
        return [
            Visits.find({
                _id: id,
                status: 1
            }),
            Persons.find({ _id: r.person._id }),
            // PersonDepartmentPay.find({person:r.person});
        ];
    }

})