Meteor.publish("doctor", function(uid) {

    return [
        Doctors.find({
            userid: uid
        }),
    ];
});
Meteor.publish("doctorReferrals", function(uid) {
	return Referrals.find({"master.userid":uid,status:1});
});
Meteor.publish("mydocInfo", function(uid) {
	console.log(uid);
    return Doctors.find({
            userid: uid
        });
});
