Meteor.methods({

    saveCard: function(args) {
        var obj = Bankcards.findOne({userid: args.userid});
        if (obj) {
            Bankcards.update({
                _id: obj._id
            }, {
                $set: {
                    "num": args.num,
                    "bank": args.bank,
                    "type": args.type,
                    createAt: new Date(),
                }
            })
        } else {
            Bankcards.insert({
                "num": args.num,
                "bank": args.bank,
                "type": args.type,
                userid: args.userid,
                createAt: new Date(),
            })
        }


    }

});
