Meteor.publish("referralList", function(uid) {
    return Referrals.find({
        "creater.userid": uid,
        status: 1
    });
})
Meteor.publish("referralDetail", function(id) {
    var r = Referrals.findOne({
        _id: id,
        status: 1
    });
    if (r) {
        return [
            Referrals.find({
                _id: id,
                status: 1
            }),
            Persons.find({ _id: r.person._id }),
            // PersonDepartmentPay.find({person:r.person});
        ];
    }

})
Meteor.publish("referralPrice", function(id) {
    var r = Referrals.findOne({
        _id: id,
        status: 1
    });
    if (r) {
        return [
            Departments.find({
                _id: r.master.department,
                status: 1
            }),

        ];
    }

})

Meteor.publish("myReferrals", function(uid) {
    return Referrals.find({"creater.userid":uid,status:1});
});