Meteor.publish("news", function() {
    return News.find({

    }, {
        sort: {
            createAt: -1
        },
        limit: 10
    });
});

Meteor.publish("newsData", function(newsid, userid) {
    if (userid)
        return [
            News.find({_id: newsid}),
            NewsComment.find({ news: newsid } ),
            NewsFovar.find({ "userid": userid, news: newsid })
        ];
    else
        return [
           News.find({_id: newsid}),
            NewsComment.find({ news: newsid } ),
        ];
});

Meteor.publish("mynews", function(userid) {
    console.log(userid);
    return [
            NewsFovar.find({ "userid": userid})
        ];
});