FlowRouter.route('/visit', {
  action: function(params, queryParams) {
    FlowLayout.render("visit");
  }
});
FlowRouter.route('/visit/detail', {
  action: function(params, queryParams) {
    FlowLayout.render("visitDetail");
  }
});
FlowRouter.route('/visit/view', {
  action: function(params, queryParams) {
    FlowLayout.render("visitView");
  }
});
