Meteor.methods({
    uploadPicForReferral: function(args) {
        var obj = Referrals.findOne({ _id: args._id });
        if (obj.person.pictures) {
            for (var i = 0; i < obj.person.pictures.length; i++) {
                var pic = obj.person.pictures[i];
                if (pic.indexOf("base64")) {
                    uploadPicForReferral(obj, i);
                }
            }
        }

    },
    queryDepartmentPrice: function(args) {
        var r = Referrals.findOne({
            _id: args.ref,
            status: 1
        });
        console.log(Departments.findOne({
            _id: r.master.sdepartment,
            status: 1
        }));

        if (r) {
            return Departments.findOne({
                _id: r.master.sdepartment,
                status: 1
            });
        }


    },
    paySuccessReferral: function(args) {
        console.log(args);
        Referrals.update({ _id: args.ref }, { $set: { checkPay: 1, payAt: new Date() } });
        //选出最少接单的人

    },
    sendInfo: function(args) {

        sms.send(args.tel, args.username + ",您好,您的转诊单已被接收");

        return "SUCCESS";
    },

});
uploadPicForReferral = function(obj, i) {
    var pic = obj.person.pictures[i];
    upload(pic, function(data) {
        var modify = { "person": {} };
        modify["person"]["pictures." + i] = data;
        Referrals.update({
            _id: obj._id
        }, {
            $set: modify
        })
    })
}
