Meteor.methods({
    createPerson: function(args) {
        //根据身份证号码判定是否同一个用户

        var p = Persons.findOne({ no: args.no });
        if (p) {

            //更新内容,不为空则更新
            var modify = {};
            if(args.name){
                modify.name = args.name
            }
            if(args.address){
                modify.address = args.address
            }
            if(args.blood){
                modify.blood = args.blood
            }
            if(args.chronic){
                modify.chronic = args.chronic
            }
            if(args.other){
                modify.other = args.other
            }


            //如果档案已经存在则加入可访问列表
            Persons.update({ _id: p._id }, {
                $addToSet: {
                    users: args.userid
                },
                $set:modify
            });


        } else {
            //不存在则建立新的档案
            var obj = {
                name: args.name,
                sex: args.sex,
                brithday: args.brithday,
                no: args.no,
                address: args.address,
                tel: args.tel,
                urgent: args.urgent,
                blood: args.blood,
                rh: args.rh,
                chronic: args.chronic,
                allergy: args.allergy,
                other: args.other,
                users: [args.userid],
                nickname: args.nickname,
                avatar: args.avatar,
                createAt: new Date(),
                status: 1,
                updateAt: new Date(),
            };
            Persons.insert(obj);
        }

    },

});
