Meteor.methods({
    uploadDoctorsPictures: function(args) {
        var obj = Doctors.findOne({
            _id: args._id
        });
        if(obj.avatar.indexOf("base64")){
        	upload(obj.avatar, function(data) {
        		Doctors.update({_id: args._id
	            }, {
	                $set: {
	                    "avatar": data
	                }
	            });
	        }, function(e) {
	            throw e;
	        });
        }

        if(obj.cert.indexOf("base64")){
        	upload(obj.cert, function(data) {
        		Doctors.update({_id: args._id
	            }, {
	                $set: {
	                    "avatar": data
	                }
	            });
	        }, function(e) {
	            throw e;
	        });
        }
        
    },

});