Meteor.methods({

    queryRegions:function(args){
    	console.log( Regions.find({parent:args.parent}).fetch());
    	return Regions.find({parent:args.parent}).fetch();
    },
    queryZones:function(args){
        return Regions.find({parent:args.region}).fetch();
    },
    queryHospitals:function(args){
    	return Hospitals.find({region:args.region}).fetch();
    },
    queryDepartments:function(args){
    	return Departments.find({hospital:args.hospital}).fetch();
    },
    querySDepartments:function(args){
        return Departments.find({parent:args.parent}).fetch();
    },
});
