Meteor.methods({
    updateDiscuzImg:function(args){
        console.log(args);
        var obj = DiscuzMessages.findOne({
            _id: args.dmid
        });
        upload(obj.pic, function(data) {
            DiscuzMessages.update({
                _id: args.dmid
            }, {
                $set: {
                    "pic": data
                }
            });
        }, function(e) {
            throw e;
        });
    },
    updateUserDiscuz:function(args){
        var d  = Discuzs.findOne({_id:args.did});
        if(args.type == "create"){
            Meteor.users.update({_id:d.userid},{$inc:{createDiscuz:1}});
        }else if(args.type == "view"){
            Meteor.users.update({_id:d.userid},{$inc:{viewDiscuz:1}});
        }else if(args.type == "agree"){
            Meteor.users.update({_id:d.userid},{$inc:{agreeDiscuz:1}});
        }else if(args.type == "comment"){
            Meteor.users.update({_id:d.userid},{$inc:{commentDiscuz:1}});
        }
    }
});
